html, body {
  font-size: 16px;
  color: white;
  line-height: 1.5;
  min-height: 100vh;
  font-family: 'Osaka';
  letter-spacing: 2px;
}

img {
  width: 100%;
  height: auto;
}

a {
  color: white;
  text-decoration: none;
}

li {
  list-style: none;
}

ul, dl, dd {
  margin: 0;
}

h2, h3, h4 {
  margin: 0;
}

p {
  margin: 0;
}

.cmn-section {
  border-bottom: 1px solid #fff;
}

.top-section {
  padding-top: 99.75px;
}

@media (max-width: 767px) {
  .top-section {
    padding-top: 90.33px;
  }
}

.about-section, .news-section, .service-section, .plan-section, .trainer-section {
  background-color: #3E3E3E;
}

.last-section {
  position: relative;
  background-color: #000;
  height: 750px;
}

@media (max-width: 767px) {
  .last-section {
    height: 550px;
  }
}

.corner-section, .personal-section, .supplements-title-section, .supplements-section {
  background-color: #000;
}

.humburger-btn {
  position: relative;
  width: 50px;
  height: 44px;
  cursor: pointer;
}

.humburger-btn span {
  position: absolute;
  left: 0;
  width: 100%;
  height: 4px;
  background-color: #fff;
  border-radius: 4px;
}

.humburger-btn, .humburger-btn span {
  transition: all 0.5s ease-in-out;
  box-sizing: border-box;
}

.humburger-btn span:nth-of-type(1) {
  top: 0;
}

.humburger-btn span:nth-of-type(2) {
  top: 20px;
}

.humburger-btn span:nth-of-type(3) {
  bottom: 0;
}

.humburger-btn[aria-expanded="true"] span:nth-of-type(1) {
  transform: translateY(20px) rotate(-45deg);
}

.humburger-btn[aria-expanded="true"] span:nth-of-type(2) {
  opacity: 0;
}

.humburger-btn[aria-expanded="true"] span:nth-of-type(3) {
  transform: translateY(-20px) rotate(45deg);
}
